a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Golos";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/GolosText-Regular.woff2") format("woff2"), url("../fonts/GolosText-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Golos";
  font-weight: 600;
  font-style: normal;
  src: url("../fonts/GolosText-DemiBold.woff2") format("woff2"), url("../fonts/GolosText-DemiBold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  min-width: 320px;
  margin: 0 auto;
  padding: 0 16px;
}
@media (min-width: 768px) {
  .container {
    min-width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1200px) {
  .container {
    min-width: 1280px;
    max-width: 1440px;
    padding: 0 120px;
  }
}

.page {
  height: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  color: #1e1e1e;
  background-color: #ffffff;
  font-family: "Golos", "Arial", sans-serif;
  font-size: 15px;
  line-height: 1.5em;
  font-style: normal;
  font-weight: 400;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  font-size: 1em;
  border-radius: 2px;
  background-color: rgba(0, 1, 3, 0);
}

.btn__secondary {
  display: block;
  text-align: center;
  color: #ffffff;
  background-color: #003154;
  width: 100%;
  padding: 12px 0;
  margin: 0 auto;
  transition: all 0.3s ease-in-out;
}
.btn__secondary:hover {
  outline: 2px solid #003154;
  background-color: #ffffff;
  color: #003154;
}
.btn__secondary:focus {
  opacity: 0.8;
}

.search {
  display: flex;
}
@media (min-width: 768px) {
  .search {
    order: 1;
  }
}

.search__form {
  position: relative;
  padding-right: 40px;
}
@media (min-width: 1200px) {
  .search__form {
    padding-right: 0;
  }
}

.search__input {
  transition: width 0.3s, border-radius 0.3s, background-color 0.3s, box-shadow 0.3s;
  width: 30px;
  height: 30px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  background: #ffffff;
}
.search__input:hover {
  color: #ffffff;
  background-color: #8f9090;
}
.search__input:focus {
  transition: width 0.3s cubic-bezier(0, 1.22, 0.66, 1.39), border-radius 0.3s, background-color 0.3s;
  border: none;
  outline: none;
  box-shadow: none;
  padding-left: 15px;
  cursor: text;
  min-width: 180px;
  border-radius: auto;
  background: rgb(235, 235, 235);
  color: black;
}

.search__submit {
  position: absolute;
  left: -5000px;
}

.search__label {
  position: relative;
}
.search__label::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  background-image: url("../img/icon/search-icon.svg");
  background-repeat: no-repeat;
}

.page-header {
  position: relative;
}

.page-header__wrapper {
  padding-top: 1em;
}

.page-header__wrap {
  position: relative;
  display: flex;
}

.page-header__logo-sub {
  display: none;
}
@media (min-width: 768px) {
  .page-header__logo-sub {
    display: block;
    font-size: 11px;
    line-height: 17px;
    font-weight: 400;
    color: #8f9090;
    margin: 0;
  }
}

.page-header__toggle {
  position: absolute;
  top: 4px;
  right: 0;
  z-index: 210000;
  width: 20px;
  height: 20px;
  cursor: pointer;
  border: none;
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  background-color: rgba(0, 1, 3, 0);
}
.page-header__toggle span {
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  background: #000103;
  border-radius: 9px;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}
.page-header__toggle--close span {
  background-color: #ffffff;
}
.page-header__toggle--close span:first-child {
  top: 6px;
  transform: rotate(135deg);
}
.page-header__toggle--close span:nth-child(2) {
  opacity: 0;
  left: -60px;
}
.page-header__toggle--close span:last-child {
  top: 6px;
  transform: rotate(-135deg);
}
.page-header__toggle--open span:first-child {
  top: 0px;
}
.page-header__toggle--open span:nth-child(2) {
  top: 6px;
}
.page-header__toggle--open span:last-child {
  top: 12px;
}
.page-header__toggle--open--nojs {
  display: none;
}
@media (min-width: 768px) {
  .page-header__toggle--open {
    display: none;
  }
}

.page-header__logo {
  flex: 1 1 auto;
}
@media (min-width: 768px) {
  .page-header__logo {
    flex: 1 0 auto;
    padding-right: 24px;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    width: 200px;
    height: 32px;
  }
}

.page-header__top-nav {
  margin: 0;
  padding: 0;
  list-style: none;
}

.top-nav {
  position: fixed;
  inset: 0;
  min-height: 42px;
  z-index: 9999;
  width: 100%;
  background-color: #003154;
  overflow-y: hidden;
}
@media (max-width: 767px) {
  .top-nav--closed {
    display: none;
  }
  .top-nav--opened {
    position: absolute;
    top: -50%;
    right: 0;
    left: 70px;
    z-index: 10;
    min-height: 30vh;
    background-color: #d0d0d0;
  }
  .top-nav--opened .top-nav__item--mail {
    padding-left: 0;
  }
  .top-nav--opened .top-nav__item--mail::before {
    display: none;
  }
  .top-nav--opened .top-nav__item a,
.top-nav--opened .top-nav__item button {
    position: relative;
    display: block;
    padding: 12px 21px;
    padding-bottom: 13px;
    font-size: 18px;
    line-height: 24px;
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    background-color: #003154;
    border-top: 1px solid #174463;
    border-bottom: 1px solid #174463;
  }
  .top-nav--nojs {
    display: block;
  }
}
@media (min-width: 768px) {
  .top-nav {
    position: relative;
    background-color: #ffffff;
    display: flex;
    justify-content: end;
  }
}

.top-nav__item--mail {
  position: relative;
  padding-left: 20px;
}
.top-nav__item--mail::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  width: 17px;
  height: 20px;
  background: url("../img/icon/mail-icon.svg") no-repeat;
  z-index: 999;
}
@media (min-width: 768px) {
  .top-nav__item {
    padding-right: 25px;
  }
}

.top-nav__link {
  font-size: 14px;
  line-height: 22px;
  font-weight: 400;
  color: #ffffff;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .top-nav__link {
    color: #003154;
  }
  .top-nav__link:hover {
    color: #8f9090;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  gap: 1px;
